.selectable {
    transition-duration: 0.4s;
    
    cursor: pointer;
}

.selected {
    background-color: #fafafa;
    color: #ffc107;
    transition: all ease-in-out 0.2s;
    transform: translateX(15px);
}

.selected img {  
    -webkit-filter: invert(85%) sepia(43%) saturate(3521%) hue-rotate(352deg) brightness(102%) contrast(104%);  
            filter: invert(85%) sepia(43%) saturate(3521%) hue-rotate(352deg) brightness(102%) contrast(104%);
}

.selectable:hover {
    color: #ffc107;
    transition: all ease-in-out 0.2s;
    
    transform: translateX(15px);
}

.selectable span {
    cursor: pointer;
}

.selectable:hover:active{
    transform: translate(15px,10px);
}

.selectable span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.3s;
}

.selectable:hover span {
    padding-right: 25px;
}

.selectable:hover span:after {
    opacity: 1;
    right: 0;
}
.selectable:hover img {
    
    -webkit-filter: invert(85%) sepia(43%) saturate(3521%) hue-rotate(352deg) brightness(102%) contrast(104%);
    
            filter: invert(85%) sepia(43%) saturate(3521%) hue-rotate(352deg) brightness(102%) contrast(104%);
}

.dashboard {
    height: 17px;
    width: 15px;
    padding-bottom: 2px;
    position: relative;
}
.tbe-selectable {
    transition-duration: 0.4s;
    
    cursor: pointer;
}

.tbe-selected {
    color: #ffc107;
    transition: all ease-in-out 0.2s;
    border-bottom-color: #ffc107;
    border-bottom-style: solid;
}

.tbe-selectable:hover {
    color: #ffc107;
    transition: all ease-in-out 0.2s;
    border-bottom-color: #ffc107;
    border-bottom-style: solid;
}

.tbe-selectable h6 {
    cursor: pointer;
}

.tbe-selectable:hover:active{
    transform: translateY(9px);
}

.tbe-selectable h6:after {
    content: '🡣';
    position: absolute;
    opacity: 0;
    right: 60px;
    transition: 0.3s;
}

.tbe-selectable:hover h6:after {
    opacity: 1;
    right: 60px;
}
